<template>
	<main class="page-main main">
		<Breadcrumbs :text="text"/>
		<section class="portfolio-projects">
			<div class="site-container">
				<h1 class="portfolio-projects__title"> Примеры<span> работ</span></h1>

				<ProjectsList :myProjects="myProjects"/>

				<BasePagination v-model="page" :count="countProjects" :per-page="projectsPerPage"/>
			</div>
		</section>
		<section class="lets-talk">
			<div class="site-container">
				<div class="lets-talk__container">
					<h2 class="lets-talk__title"> Нужна помощь с проектом?<span> Напиши мне!</span></h2>
					<p class="lets-talk__text"> Lorem ipsum dolor, sit amet consectetur adipisicing elit. Quibusdam distinctio
						sunt dolor repudiandae pariatur facere.</p>
					<router-link class="button-reset button-primary lets-talk__button" :to="{name: 'contact-me'}">
						Написать мне
					</router-link>
				</div>
			</div>
		</section>
	</main>
</template>

<script>
import myProjects from '@/data/myProjects.js';
import ProjectsList from '@/components/ProjectsList';
import Breadcrumbs from "@/components/Breadcrumbs.vue"
import BasePagination from '@/components/BasePagination';

export default {
	name: 'PageMyProjects',
	components: {
		ProjectsList,
		BasePagination,
		Breadcrumbs
	},
	data() {
		return {
			page: 1,
			projectsPerPage: 4,
			text: "Проекты"
		}
	},
	computed: {
		myProjects () {
			const offset = (this.page - 1) * this.projectsPerPage;

			return myProjects.slice(offset, offset + this.projectsPerPage);
		},
		countProjects() {
			return myProjects.length;
		}
	}
}
</script>
