import Vue from 'vue';
import VueRouter from 'vue-router';
import PageMain from '@/pages/PageMain';
import PageContactMe from '@/pages/PageContactMe';
import PageMyProjects from '@/pages/PageMyProjects';
import PageAbout from '@/pages/PageAbout';
import NotFound from '@/pages/NotFound';

Vue.use(VueRouter);

const routes = [
	{
		name: 'main',
		component: PageMain,
		path: '/'
	},
	{
		name: "contact-me",
		component: PageContactMe,
		path: '/contact-me/'
	},
	{
		name: "my-projects",
		component: PageMyProjects,
		path: '/my-projects/'
	},
	{
		name: "about",
		component: PageAbout,
		path: '/about/'
	},
	{
		name: "404",
		component: NotFound,
		path: '*'
	}
];

const router = new VueRouter({
	routes
});

export default router;
