<template>
	<ul class="pagination">
		<!--
		<li class="pagination__item">
			<button class="button-reset pagination__button pagination__button_prev" @click="prevPage();">
				Пред
			</button>
		</li>
		-->
		<li class="pagination__item" v-for="pageNumber in pages" :key="pageNumber">
			<a href="" :class="{'pagination__link_current' : pageNumber === page}" @click.prevent="paginate(pageNumber);">
				{{ pageNumber }}
			</a>
		</li>
		<!--
		<li class="pagination__item">
			<button class="button-reset pagination__button pagination__button_next" @click="nextPage();">
				След
			</button>
		</li>
		-->
	</ul>
</template>

<script>
export default {
	model: {
		prop: 'page',
		event: 'paginate'
	},
	props: ['page', 'count', 'perPage'],
	computed: {
		pages() {
			return Math.ceil(this.count / this.perPage);
		}
	},
	methods: {
		paginate(page) {
			this.$emit('paginate', page);
		},
		// nextPage() {
		// 	this.page++;
		// },
		// prevPage() {
		// 	this.page--;
		// }
	}
}
</script>

