<script>
import MainPageSlider from '@/components/MainPageSlider';
import myProjects from '@/data/myProjects.js';
import ProjectsList from '@/components/ProjectsList';

export default {
	name: 'PageMain',
	components: {
		ProjectsList,
		MainPageSlider
	},
	data() {
		return {
			page: 1,
			projectsPerPage: 3,
		}
	},
	computed: {
		myProjects () {
			const offset = (this.page - 1) * this.projectsPerPage;

			return myProjects.slice(offset, offset + this.projectsPerPage);
		}
	}
}
